#IT{
  overflow: hidden;
  width: 100vw;
  max-width: 65rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 0 auto;
}

.skillsTitle{
  font-size: 2.4rem;
  font-weight: 600;
  padding-top: 3rem;
  margin-bottom: 1.5rem;
}

.skillDescription{
  font-weight: 20;
  font-size: 1rem;
  max-width: 50rem;
  padding: 0 2rem;
}

.experienceContainer{
  color: white;
  width:80vw;
  border-radius: 0.6rem;
  padding: 1rem 0.5rem;
  margin: 3rem 0rem;
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: flex-start;
  text-align: left;
  background-color: rgb(50,50,50);
}

.imageContainer{
  width:10vw;
}

.subtitle{
  font-size: 1.5rem;
  color: #6c63ff;
}

.gif{
  height: 7rem;
  margin: 1rem 0rem;
  border-radius: 1rem;
}

.description{
  display: flex;
  flex-direction: column;
}

.companyTitle{
  display: flex;
  flex-direction: row;
  justify-items: center;
  align-items: center;
  padding: 1rem 0rem;
}

.companyName{
  font-size: 2.4rem;
  font-weight: 600;
}

.companyTitleDescription{
  margin: 1rem ;
  font-style: italic;
}

.descriptionCompany{
  font-size: 1.2rem;
  margin: 1rem 0rem;
}

